<template>
  <div id="chargeMBox">
    <div class="pay">
      <img
        src="../../assets/order/icon_ele.png"
        alt=""
      >
      <span>预付电费</span>
    </div>
    <div
      class="info"
      v-if="roomInfo"
    >
      <p class="mleft">房间信息</p>
      <p class="mright">{{ roomInfo.hotelName }}-{{ roomInfo.scaleName }}-{{ roomInfo.virscaleName }}-{{ roomInfo.roomSn }}</p>
    </div>
    <div
      class="info border_b"
      v-if="currentInfo"
    >
      <p class="mleft">电费单价</p>
      <p class="mright">{{ currentInfo.price }}元/度</p>
    </div>
    <div
      class="info"
      v-if="currentInfo"
    >
      <p class="mleft">剩余电费</p>
      <p class="mright">
        <span class="f1">{{ currentInfo.currentAmount }}</span>
        <span>元</span>
      </p>
    </div>
    <div class="info">
      <mt-button
        class="mtleft"
        @click="back"
      >充值记录</mt-button>
      <mt-button
        class="mtright"
        @click="eleTotal"
      >用电统计</mt-button>
    </div>
  </div>
</template>
<script>
import { GetBusinessElecBillSurplusByorderroom } from '@/api/api_business_hotel_and_scale';
export default {
    data() {
        return {
            currentInfo: null,
            roomInfo: null
        };
    },
    created() {
        this.$store.commit('setHeader', '电费管理');
        this.roomInfo = this.$route.query;
        this.getCurrentInfo();
    },
    methods: {
        getCurrentInfo() {
            GetBusinessElecBillSurplusByorderroom(this.$route.query.orderRoomId).done((res) => {
                this.currentInfo = res.data;
            });
        },
        // 充值记录
        back() {
            this.$router.push({
                path: '/eleRecord',
                query: {
                    id: this.$route.query.orderRoomId
                }
            });
        },
        // 用电统计
        eleTotal() {
            this.$router.push({
                path: '/eleTotal',
                query: {
                    orderRoomId: this.$route.query.orderRoomId
                }
            });
        }
    }
};
</script>
<style scoped>
#chargeMBox {
    background: #fff;
    margin: 10px;
    font-size: 0.8rem;
    color: #888;
}
#chargeMBox > .pay {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 16px;
    color: #000;
    font-weight: bold;
}
#chargeMBox > .pay > img {
    width: 18px;
    padding-right: 8px;
}
#chargeMBox > .info {
    display: flex;
    justify-content: space-between;
    margin: 8px 10px;
}
#chargeMBox > .info > .mright {
    color: #000;
}
#chargeMBox > .info > .mtleft {
    width: 45%;
    margin: 20px 0 10px;
    background: #ff7d41;
    color: #fff;
    font-size: 0.8rem;
}
#chargeMBox > .info > .mtright {
    width: 45%;
    margin: 20px 0 10px;
    background: #fff;
    color: #ff7d41;
    font-size: 0.8rem;
    border: 2px solid #ff7d41;
}
.border_b {
    padding-bottom: 20px;
    border-bottom: 1px solid #f6f6f6;
}
.mleft {
    width: 30%;
}
.mright {
    width: 70%;
    text-align: right;
}
.f1 {
    font-size: 1.2rem;
    font-weight: bold;
}
</style>



